import { CdkDragDrop, CdkDrag, copyArrayItem, moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  Words = ['','','','','','','','','','','','','','','','','','','','','','','','','','','','','',''];
  done = [
    '<img src="http://pic.616pic.com/ys_bnew_img/00/11/72/GNfI4FZXhC.jpg" width="100px">',
    '<img src="https://cdn.loveiizakka.com/img?i=5b62df1c-eb96-410c-a77f-503d14bc7369&c=01d3" width="100px">',
    '<img src="https://img.lovepik.com/element/40145/7293.png_860.png" width="100px">',
    '<img src="https://lh3.googleusercontent.com/proxy/DzH4PmkYcoQ4cnk9yHY2FPjGy6ShyBevZcBdTOUSmj76TnBnKvvUcDaP_iAcQo1k2aeZOjtrSGYHxHzZUvM1ec3hxdrvnq1w1N1rtQ" width="100px">',
    '<img src="http://pic.90sjimg.com/design/00/14/27/32/5938ac6e7c642.png" width="100px">'
  ];
  title = 'drop';

  drop(event: CdkDragDrop<any>) {
    if (event.container.connectedTo.toString() === event.previousContainer.connectedTo.toString()) {
      if(event.container.connectedTo.toString()==='left'){
        this.Words[event.previousContainer.data.index]=event.container.data.item;
        this.Words[event.container.data.index]=event.previousContainer.data.item;
      }
    }else {
      if(event.container.connectedTo.toString()==='left'){
        this.Words[event.container.data.index]=event.previousContainer.data.item;
      }
    }
  }
}
